import './style';
import { render } from 'preact';
import { useEffect, useState, useCallback } from 'preact/hooks';

let items = [];

const Filter = ({ filterValue, setFilterValue, setItemsFiltered }) => {
	const handleChange = useCallback((e) => {
	    console.log(items);
		setFilterValue(e.target.value);
		setItemsFiltered(items.filter(item => (item[1] + ' ' + item[2]).toLocaleLowerCase().indexOf(e.target.value) !== -1));
	}, []);
	return <div><input value={filterValue} onInput={handleChange} placeholder="Filter" /></div>;
};

const Item = ({ item }) => (
	<div>
		<span>Brand: { item[1] }</span>
		<br />
		<span>Model: { item[2] }</span>
		<div>
			<img src={'http://lamptest.ru/images/photo/' + item[52] + '.jpg'} />
		</div>
	</div>
);

const App = () => {
	const [itemsFiltered, setItemsFiltered] = useState([]);
	const [item, setItem] = useState(null);
	const [filterValue, setFilterValue] = useState('');

	useEffect(() => {
		const myHeaders = new Headers();
		myHeaders.append('Content-Type','text/csv; charset=windows-1251');

		fetch('https://lamptest.ru/led.php', myHeaders).then(response => response.text()).then((response) => {
			const itemsS = response.split('\n').map(item => item.split(';')).slice(1);
			items = JSON.parse(JSON.stringify(itemsS));
			setItemsFiltered(JSON.parse(JSON.stringify(itemsS)));
		});
	}, []);


	return (
		<div class="table">
			<div class="sidebar">
				<Filter filterValue={filterValue} setFilterValue={setFilterValue} setItemsFiltered={setItemsFiltered} />
				{ itemsFiltered.map((item) => <div class="sidebar-item" onClick={() => setItem(item)}>{ item[1] } { item[2] }</div>) }

			</div>
			<div class="content">
				{ item && <Item item={item} /> }
			</div>
		</div>
	);
};


if (typeof window !== 'undefined') {
	render(<App />, document.body);
}



